<script setup>
import { ref, onMounted, computed, nextTick } from "vue";
import { useRoute, useRouter } from "vue-router";
// Store
import { useAppStore } from "@/store/app/app.js";
const appStore = useAppStore();
const route = useRoute();
// Functions
import { lmnFetch } from "@/assets/js/functions/lmnFetch.js";
import { notify } from "@/assets/js/functions/notify.js";
import { inSearch } from "@/assets/js/functions/inSearch.js";
import { handleTokenExpiration } from "@/assets/js/functions/handleTokenExpiration.js";
// Components
import AppIcon from "@/components/utility/icons/index.vue";
import Confirm from "@/components/utility/confirm/index.vue";
import AppButton from "@/components/utility/app-button/index.vue";
import AppCheckBox from "@/components/utility/checkbox-basic/index.vue";
import AppInput from "@/components/utility/text-input/index.vue";
import AppTextarea from "@/components/utility/text-area/index.vue";
import Modal from "@/components/utility/modal/index.vue";
import { commandsList } from "./src/commandsList.js";

// State
const activated = ref(false);
const confirm = ref(null);
const router = useRouter();
const search = ref("");
const commandInput = ref("");
const terminalOutput = ref([]);
const commandHistory = ref([]);
const historyPosition = ref(-1);
const commands = ref([]);
const commandsFlat = ref([]);
const loading = ref(false);
const commandsLoading = ref(false);
const selectedCommandInfo = ref(null);
const terminalElement = ref(null);

// Computed
const filteredCommands = computed(() => {
  if (!search.value) return commandsList;

  return commandsList.filter((cmd) => inSearch(search.value, cmd.command, cmd.description));
});

// Get CLI activation state
const getActivationState = async () => {
  appStore.updateState("loading", true);
  const args = {
    endpoint: `snipvault/v1/cli/status`,
  };
  const response = await lmnFetch(args);
  appStore.updateState("loading", false);
  if (!response) return;

  if (response.data.active) {
    // Set timer for expiration
    handleTokenExpiration(response.data.expires, showTokenExpiryModal, (secondsRemaining) => {});
    activated.value = true;
    fetchEnviromentData();
    return;
  }

  // Confirm user intent
  const userResponse = await confirm.value.show({
    title: __("Enable CLI interface?", "snipvault"),
    message: __("For security reasons, the CLI requires temporary activation. Once activated, it will remain available for one hour and then automatically deactivate.", "snipvault"),
    okButton: __("Activate for 1 Hour", "snipvault"),
  });

  // Bailed by user
  if (!userResponse) {
    router.push(`/`);
    return;
  }

  activateCLI();
};

// Activate CLI
const activateCLI = async () => {
  appStore.updateState("loading", true);
  const args = {
    endpoint: `snipvault/v1/cli/enable`,
    type: "POST",
  };
  const response = await lmnFetch(args);
  appStore.updateState("loading", false);
  if (!response) return;

  if (response.data.success) {
    activated.value = true;
    notify({
      title: __("CLI interface enabled", "snipvault"),
      type: "success",
    });

    // Set timer for expiration
    handleTokenExpiration(response.data.expires, showTokenExpiryModal, (secondsRemaining) => {});

    fetchEnviromentData();
    return;
  }
};

const showTokenExpiryModal = async () => {
  // Confirm user intent
  const userResponse = await confirm.value.show({
    title: __("WP CLI access has expired", "snipvault"),
    message: __("For security reasons, access to the WP-CLI has now expired. Would you like to extend access for one hour?", "snipvault"),
    okButton: __("Activate for 1 Hour", "snipvault"),
  });

  if (userResponse) {
    activateCLI();
  }
};

// Fetch available commands
const fetchEnviromentData = async () => {
  const args = {
    endpoint: `snipvault/v1/cli/commands`,
  };
  const response = await lmnFetch(args);

  if (!response || !response.data.success) {
    addToTerminal("Error fetching available commands.", "error");
    return;
  }

  processCommandData(response.data);
};

// Process command data from response
const processCommandData = (data) => {
  // Add welcome message
  if (data.version) {
    addToTerminal(`WP-CLI ${data.version.version}`, "info");
  } else {
    addToTerminal("WP-CLI interface ready", "info");
  }

  // Add system info
  if (data.info) {
    const infoText = `PHP: ${data.info.php_version} | MySQL: ${data.info.mysql_version} | WordPress: ${data.info.wp_version}`;
    addToTerminal(infoText, "system");
  }

  addToTerminal("This feature may not work on all hosts.", "error");
  addToTerminal("Type 'help' to see available commands or select from the left panel.", "help");
};

// Flatten command structure for search
const flattenCommands = (commandsObj, prefix = "") => {
  commandsFlat.value = [];

  const processCommand = (cmd, path = "") => {
    const fullPath = path ? `${path} ${cmd}` : cmd;
    commandsFlat.value.push(fullPath);

    if (commandsObj[cmd] && commandsObj[cmd].subcommands) {
      Object.keys(commandsObj[cmd].subcommands).forEach((sub) => {
        processCommand(sub, fullPath);
      });
    }
  };

  Object.keys(commandsObj).forEach((cmd) => {
    processCommand(cmd);
  });
};

// Execute CLI command
const executeCommand = async (cmd = null) => {
  const command = cmd || commandInput.value.trim();

  if (!command) return;

  // Add to history
  commandHistory.value.unshift(command);
  if (commandHistory.value.length > 50) {
    commandHistory.value.pop();
  }
  historyPosition.value = -1;

  // Special case for 'clear' command
  if (command.toLowerCase() === "clear") {
    terminalOutput.value = [];
    commandInput.value = "";
    return;
  }

  // Special case for 'help' command
  if (command.toLowerCase() === "help") {
    showHelpMessage();
    commandInput.value = "";
    return;
  }

  // Add command to terminal
  addToTerminal(`$ ${command}`, "command");

  // Execute command - remove 'wp' prefix if present for API call
  loading.value = true;
  commandInput.value = "";

  // Remove 'wp' prefix for API call to avoid duplication
  const isHelpCommand = command.trim() === "" || command.trim() === "help" || command.includes("--help") || command.includes(" -h") || command === "-h";

  let apiCommand = command;
  if (!isHelpCommand) {
    apiCommand = command.startsWith("wp ") ? command.substring(3) : command;
  }

  // Create form data for the request
  const formData = new FormData();
  formData.append("action", "snirvault_run_wp_cli");
  formData.append("security_nonce", appStore.state.ajaxNonce);
  formData.append("command", apiCommand);

  // Make the request to admin-ajax.php

  try {
    const response = await fetch(appStore.state.ajaxURL, {
      method: "POST",
      credentials: "same-origin", // Include cookies for authentication
      body: formData,
    });

    loading.value = false;

    // Check if the request was successful
    if (!response.ok) {
      try {
        const data = await response.json();
        addToTerminal(`${data.data.message} Status: ${response.status}`, "error");
      } catch (err) {
        addToTerminal(`HTTP error Status: ${response.status}`, "error");
      }

      return;
    }

    // Parse the JSON response
    const data = await response.json();

    // Show output
    if (data.success) {
      if (data.output.trim()) {
        addToTerminal(data.output.trim(), "output");
      } else {
        addToTerminal("Command executed successfully (no output).", "success");
      }
    } else {
      if (data.error) {
        addToTerminal(data.error, "error");
      } else {
        addToTerminal("Command failed with no error message.", "error");
      }
    }
  } catch (err) {
    addToTerminal(`Unable to connect to wp cli right now`, "error");
  }

  // Scroll terminal to bottom
  scrollToBottom();
};

// Add text to terminal
const addToTerminal = (text, type = "output") => {
  terminalOutput.value.push({ text, type, id: Date.now() });
  scrollToBottom();
};

// Scroll terminal to bottom
const scrollToBottom = async () => {
  await nextTick();
  if (terminalElement.value) {
    terminalElement.value.scrollTop = terminalElement.value.scrollHeight;
  }
};

// Command history navigation
const navigateHistory = (direction) => {
  if (commandHistory.value.length === 0) return;

  if (direction === "up") {
    historyPosition.value = Math.min(commandHistory.value.length - 1, historyPosition.value + 1);
  } else {
    historyPosition.value = Math.max(-1, historyPosition.value - 1);
  }

  if (historyPosition.value === -1) {
    commandInput.value = "";
  } else {
    commandInput.value = commandHistory.value[historyPosition.value];
  }
};

// Handle terminal keydown
const handleKeyDown = (e) => {
  if (e.key === "Enter") {
    executeCommand();
  } else if (e.key === "ArrowUp") {
    e.preventDefault();
    navigateHistory("up");
  } else if (e.key === "ArrowDown") {
    e.preventDefault();
    navigateHistory("down");
  } else if (e.key === "Tab") {
    e.preventDefault();
    autocompleteCommand();
  }
};

// Autocomplete command
const autocompleteCommand = () => {
  const input = commandInput.value.trim();
  if (!input) return;

  const matches = commandsFlat.value.filter((cmd) => cmd.toLowerCase().startsWith(input.toLowerCase()));

  if (matches.length === 1) {
    commandInput.value = matches[0];
  } else if (matches.length > 1) {
    addToTerminal("Available completions:", "system");
    matches.slice(0, 10).forEach((match) => {
      addToTerminal(`  ${match}`, "help");
    });

    if (matches.length > 10) {
      addToTerminal(`  ... and ${matches.length - 10} more`, "help");
    }

    // Find common prefix
    let commonPrefix = matches[0];
    for (let i = 1; i < matches.length; i++) {
      let j = 0;
      while (j < commonPrefix.length && j < matches[i].length && commonPrefix[j].toLowerCase() === matches[i][j].toLowerCase()) {
        j++;
      }
      commonPrefix = commonPrefix.substring(0, j);
    }

    if (commonPrefix.length > input.length) {
      commandInput.value = commonPrefix;
    }
  }
};

// Show command info
const showCommandInfo = async (command) => {
  if (!command) return;

  selectedCommandInfo.value = {
    name: command.command,
    loading: false,
    description: command.description,
  };
};

// Show help message
const showHelpMessage = () => {
  addToTerminal("Available commands:", "help");
  addToTerminal("  help - Show this help", "help");
  addToTerminal("  clear - Clear the terminal", "help");
  addToTerminal("  wp ... - Execute a WP-CLI command", "help");
  addToTerminal("", "help");
  addToTerminal("Keyboard shortcuts:", "help");
  addToTerminal("  Up/Down - Navigate command history", "help");
  addToTerminal("  Tab - Autocomplete command", "help");
  addToTerminal("", "help");
  addToTerminal("Tips:", "help");
  addToTerminal("  - Click on a command in the left panel to see its documentation", "help");
  addToTerminal("  - Press Tab to autocomplete commands", "help");
  addToTerminal("  - Add --help to any command to see its usage", "help");
};

// Run command from the example
const runExample = (example) => {
  const cmd = example.replace(/^\$\s+/, "").replace(/^wp\s+/, "");
  commandInput.value = cmd;
};

// Setup component
onMounted(() => {
  getActivationState();
});
</script>

<template>
  <div class="flex flex-row h-full max-h-full overflow-hidden grow">
    <!-- Command List Sidebar -->
    <div class="w-[300px] border-r border-zinc-200 dark:border-zinc-700 h-full max-h-full shrink-0">
      <div class="flex flex-col h-full max-h-full overflow-hidden relative">
        <!-- Search bar -->
        <div class="relative w-full border-b border-zinc-200 dark:border-zinc-700 flex flex-row pr-4 items-center gap-1">
          <AppIcon icon="search" class="absolute left-5 top-1/2 -translate-y-1/2 text-zinc-500" />
          <input type="text" v-model="search" :placeholder="__('Search commands...', 'snipvault')" class="px-6 py-4 pl-12 w-full bg-white dark:bg-zinc-900 focus:outline-0 grow" />
        </div>

        <!-- Command List -->
        <div class="overflow-y-auto flex-grow">
          <div v-if="commandsLoading" class="p-4 text-center text-zinc-500">
            <AppIcon icon="refresh" class="animate-spin inline-block mr-2" />
            {{ __("Loading commands...", "snipvault") }}
          </div>
          <div v-else-if="filteredCommands.length === 0" class="p-4 text-center text-zinc-500">
            {{ search ? __("No matching commands found", "snipvault") : __("No commands available", "snipvault") }}
          </div>
          <div v-else class="command-list">
            <div
              v-for="command in filteredCommands"
              :key="command"
              @click="showCommandInfo(command)"
              class="px-4 py-2 hover:bg-zinc-100 dark:hover:bg-zinc-800 cursor-pointer border-b border-zinc-100 dark:border-zinc-800 flex items-center"
            >
              <AppIcon icon="terminal" class="mr-2 text-zinc-400" />
              <span v-html="command.command"></span>
            </div>
          </div>
        </div>
      </div>
    </div>

    <!-- Terminal and Documentation -->
    <div class="flex flex-col flex-grow overflow-hidden grow">
      <!-- Command Documentation (if selected) -->
      <div v-if="selectedCommandInfo" class="border-b border-zinc-200 dark:border-zinc-700 p-4 overflow-y-auto max-h-[40%] shrink-0">
        <div class="flex justify-between items-center mb-4">
          <h2 class="text-lg font-semibold">{{ selectedCommandInfo.name }}</h2>
          <AppButton @click="selectedCommandInfo = null" type="transparent">
            <AppIcon icon="close" />
          </AppButton>
        </div>

        <div v-if="selectedCommandInfo.loading" class="p-4 text-center text-zinc-500">
          <AppIcon icon="refresh" class="animate-spin inline-block mr-2" />
          {{ __("Loading documentation...", "snipvault") }}
        </div>

        <div v-else>
          <div v-if="selectedCommandInfo.description" class="mb-4">
            <p>{{ selectedCommandInfo.description }}</p>
          </div>

          <div v-if="selectedCommandInfo.usage" class="mb-4">
            <h3 class="font-medium mb-1">{{ __("Usage:", "snipvault") }}</h3>
            <div class="bg-zinc-100 dark:bg-zinc-800 p-2 rounded font-mono text-sm">
              {{ selectedCommandInfo.usage }}
            </div>
          </div>

          <div v-if="selectedCommandInfo.options && selectedCommandInfo.options.length > 0" class="mb-4">
            <h3 class="font-medium mb-1">{{ __("Options:", "snipvault") }}</h3>
            <div class="bg-zinc-100 dark:bg-zinc-800 p-2 rounded font-mono text-sm">
              <div v-for="option in selectedCommandInfo.options" :key="option.name" class="mb-1">
                <span class="text-blue-600 dark:text-blue-400">{{ option.name }}</span>
                <span>{{ option.description }}</span>
              </div>
            </div>
          </div>

          <div v-if="selectedCommandInfo.examples && selectedCommandInfo.examples.length > 0" class="mb-4">
            <h3 class="font-medium mb-1">{{ __("Examples:", "snipvault") }}</h3>
            <div class="bg-zinc-100 dark:bg-zinc-800 p-2 rounded font-mono text-sm">
              <div
                v-for="(example, index) in selectedCommandInfo.examples"
                :key="index"
                class="flex items-center mb-1 hover:bg-zinc-200 dark:hover:bg-zinc-700 p-1 rounded cursor-pointer"
                @click="runExample(example)"
              >
                <span>{{ example }}</span>
                <AppIcon icon="play" class="ml-2 text-green-500" />
              </div>
            </div>
          </div>

          <div class="mt-4">
            <AppButton type="primary" @click="executeCommand(selectedCommandInfo.name)">
              {{ __("Run Command", "snipvault") }}
            </AppButton>
          </div>
        </div>
      </div>

      <!-- Terminal Interface -->
      <div class="flex-grow flex flex-col overflow-hidden">
        <!-- Terminal Output -->
        <div ref="terminalElement" class="flex-grow overflow-y-auto p-4 font-mono text-sm bg-zinc-900 text-zinc-200">
          <div v-if="!activated" class="text-center p-4">
            {{ __("CLI interface is not activated", "snipvault") }}
          </div>

          <div v-for="entry in terminalOutput" :key="entry.id" class="mb-1">
            <span
              :class="{
                'text-green-400': entry.type === 'command',
                'text-red-400': entry.type === 'error',
                'text-blue-400': entry.type === 'info',
                'text-yellow-400': entry.type === 'help',
                'text-purple-400': entry.type === 'system',
                'text-green-300': entry.type === 'success',
              }"
            >
              <template v-if="entry.type === 'command'">{{ entry.text }}</template>
              <pre v-else v-html="entry.text"></pre>
            </span>
          </div>

          <div v-if="loading" class="text-yellow-400 flex flex-row items-center">
            <AppIcon icon="refresh" class="animate-spin inline-block mr-2" />
            <span>{{ __("Processing...", "snipvault") }}</span>
          </div>
        </div>

        <!-- Command Input -->
        <div class="border-t border-zinc-700 flex items-center bg-zinc-900 px-4 py-2">
          <span class="text-green-400 mr-2 font-mono">$</span>
          <input
            v-model="commandInput"
            type="text"
            @keydown="handleKeyDown"
            :placeholder="__('Type a command...', 'snipvault')"
            class="bg-transparent border-none outline-none flex-grow text-zinc-200 font-mono"
            :disabled="!activated"
          />
          <AppButton type="primary" @click="executeCommand" :disabled="!activated || loading || !commandInput.trim()">
            {{ __("Run", "snipvault") }}
          </AppButton>
        </div>
      </div>
    </div>
  </div>
  <Confirm ref="confirm" />
</template>

<style scoped>
/* Scrollbar styling for terminal */
.terminal-output::-webkit-scrollbar {
  width: 8px;
}

.terminal-output::-webkit-scrollbar-track {
  background: #2d3748;
}

.terminal-output::-webkit-scrollbar-thumb {
  background-color: #4a5568;
  border-radius: 4px;
}

/* Command list styling */
.command-list {
  max-height: calc(100vh - 120px);
}
</style>
